import { useObserver } from 'mobx-react-lite'
import React from 'react'

import { IconLine } from '../../../atoms/IconLine'
import { useRoot } from '../../../atoms/RootProvider'
import { CompanyBranchPicker } from '../../../organisms/CompanyBranchPicker'

interface IProps {
  isMulti?: boolean
  width?: number
}

export const SettingsCompanyBranchPicker: React.FC<IProps> = ({
  isMulti,
  width,
}) => {
  const { settings } = useRoot()
  return useObserver(() => (
    <IconLine icon="store_mall_directory">
      <CompanyBranchPicker
        selectedBranches={settings.branchId ? [settings.branchId] : undefined}
        onSelectionChange={([branch]) => settings.selectBranch(branch)}
        isMulti={isMulti}
        isClearable={false}
        getDropdownProps={() => ({ width })}
      />
    </IconLine>
  ))
}
