import { SvgIconProps } from '@material-ui/core/SvgIcon'
import React from 'react'
import styled from 'styled-components'

import { Colors, Spacing } from '../styling'
import { Icon } from './Icon'

interface IProps {
  icon?: string | React.ComponentType<SvgIconProps>
  label?: string
  labelWidth?: number
  labelWidthUnits?: string
  labelMargin?: string
  className?: string
}

const getLabelWidth = ({
  labelWidth = 0,
  labelWidthUnits = Spacing.DefaultSizeUnit,
}: IProps) => {
  return labelWidth + labelWidthUnits
}

const getLabelMargin = ({ labelMargin = '0.5rem 0.2rem 0 auto' }: IProps) => {
  return labelMargin
}

const IconLineWrap = styled.div`
  display: flex;
`
IconLineWrap.displayName = 'IconLineWrap'

const DescriptionContainer = styled.div`
  margin: ${getLabelMargin};
  min-width: ${getLabelWidth};
  flex: 0 0 auto;
  text-align: right;
`

const IconLineChildren = styled.div`
  margin-left: 0.5rem;
  align-self: stretch;
  flex: 1 1 auto;
`
const LabelStyled = styled.div`
  padding-top: 0.2rem;
  display: inline-block;
`

IconLineChildren.displayName = 'IconLineChildren'

export const IconLine: React.FC<IProps> = ({
  icon,
  label,
  labelWidth,
  labelWidthUnits,
  labelMargin,
  children,
  ...props
}) => {
  let iconComponent
  if (icon) {
    if (typeof icon === 'string') {
      iconComponent = <Icon iconName={icon} color={Colors.greyDark} />
    } else {
      iconComponent = React.createElement(icon, {
        nativeColor: Colors.greyDark,
      })
    }
  }

  return (
    <IconLineWrap {...props}>
      <DescriptionContainer
        labelWidth={labelWidth}
        labelWidthUnits={labelWidthUnits}
        labelMargin={labelMargin}
      >
        {iconComponent}
        {label ? <LabelStyled>{label}</LabelStyled> : null}
      </DescriptionContainer>
      <IconLineChildren>{children}</IconLineChildren>
    </IconLineWrap>
  )
}
