import React from 'react'

import { PageLayout } from '../../../molecules/PageLayout'
import { PageLayoutItem } from '../../../molecules/PageLayoutItem'
import { BusinessHoursButton } from '../organisms/BusinessHoursButton'
import { MenuCategoryButton } from '../organisms/MenuCategoryButton'
import { MenuRecipeButton } from '../organisms/MenuRecipeButton'
import { MenuStockButton } from '../organisms/MenuStockButton'
import { MenuStockDepositButton } from '../organisms/MenuStockDepositButton'
import { UserMenuButton } from '../organisms/UserMenuButton'
import { ZonesMenuButton } from '../organisms/ZonesMenuButton'

interface ISettingsPageProps {}

function getLayout() {
  return [
    { i: 'BusinessHours', x: 0, y: 1, w: 2, h: 2 },
    { i: 'Preferences', x: 2, y: 1, w: 2, h: 2 },
    { i: 'ZonesMenu', x: 4, y: 1, w: 2, h: 2 },
    { i: 'UserMenu', x: 6, y: 1, w: 2, h: 2 },
    { i: 'MenuRecipe', x: 0, y: 3, w: 2, h: 2 },
    { i: 'MenuCategory', x: 2, y: 3, w: 2, h: 2 },
    { i: 'MenuStock', x: 4, y: 3, w: 2, h: 2 },
    { i: 'MenuStockDeposit', x: 6, y: 3, w: 2, h: 2 },
  ]
}

export const SettingsPage: React.FC<ISettingsPageProps> = props => (
  <PageLayout layouts={getLayout()}>
    <PageLayoutItem
      key="BusinessHours"
      route="/settings/businesshours"
      component={BusinessHoursButton}
      canQuery="businessHours"
    />
    {/* <PageLayoutItem
      key="Preferences"
      route="/settings/preferences"
      component={PreferencesButton}
    /> */}
    <PageLayoutItem
      key="MenuRecipe"
      route="/settings/menu/recipe"
      component={MenuRecipeButton}
      canQuery="recipes"
    />
    <PageLayoutItem
      key="MenuCategory"
      route="/settings/menu/category"
      component={MenuCategoryButton}
      canQuery="wareCategories"
    />
    <PageLayoutItem
      key="MenuStock"
      route="/settings/menu/stock"
      component={MenuStockButton}
      canQuery="stockItems"
    />
    <PageLayoutItem
      key="MenuStockDeposit"
      route="/settings/menu/stock/deposit"
      component={MenuStockDepositButton}
      canMutate="changeAmountStockItem"
    />
    <PageLayoutItem
      key="ZonesMenu"
      route="/settings/zones"
      component={ZonesMenuButton}
      canQuery="zones"
    />
    <PageLayoutItem
      key="UserMenu"
      route="/settings/user"
      component={UserMenuButton}
      canQuery="user"
    />
  </PageLayout>
)
