import React from 'react'
import { Checkbox, FieldType } from 'react-form'
import styled from 'styled-components'

import { Colors, Radiuses, Spacing } from '../styling'

interface IProps extends React.InputHTMLAttributes<never> {
  field: FieldType
  label?: string
}

const CheckboxStyled = styled(Checkbox)`
  -webkit-appearance: none;
  -moz-appearance: none;
  background-color: ${Colors.white};
  border: 1px solid ${Colors.greyMiddle};
  padding: 1rem;
  border-radius: ${Radiuses.TextComponent};
  display: inline-block;
  position: relative;
  margin: ${Spacing.MarginAroundFormComponents};
  outline: none;
  &:checked {
    background-color: ${Colors.white};
    border: 1px solid ${Colors.greyMiddle};
  }
  &:checked:after {
    content: '\\2714';
    font-size: 2rem;
    position: absolute;
    top: -0.5rem;
    left: 0.1rem;
    color: ${Colors.greyDark};
    overflow: hidden;
  }
`

const LabelStyled = styled.div`
  display: inline-block;
  margin: 0.4rem 0 0 0.5rem;
  vertical-align: top;
`

export const FormInputCheckbox: React.FC<IProps> = ({
  field,
  label,
  defaultValue,
  children,
  ...inputProps
}) => (
  <>
    <CheckboxStyled
      field={field}
      checked={Boolean(defaultValue)}
      {...inputProps}
    />
    {label && <LabelStyled>{label}</LabelStyled>}
  </>
)
