import { i18nMark } from '@lingui/core'
import React from 'react'

import { SelectControlFlat } from '../../../atoms/SelectControlFlat'
import { i18n } from '../../../i18n'
import { WithSettings } from '../../settings/atoms/WithSettings'
import { ESpecificHourMode } from '../models/BusinessHoursModel'

const labels = {
  [ESpecificHourMode.TIMESPAN]: i18nMark('Open between'),
  [ESpecificHourMode.FULLDAY]: i18nMark('Closed'),
}

export const SpecificModeSelector: React.FC = () => (
  <WithSettings
    observe={true}
    render={({ businessHours }) => (
      <SelectControlFlat<ESpecificHourMode>
        items={[ESpecificHourMode.TIMESPAN, ESpecificHourMode.FULLDAY]}
        selectedItem={businessHours.specificMode as ESpecificHourMode}
        formatItem={mode => i18n._(labels[mode])}
        onSingleSelection={(mode: ESpecificHourMode) =>
          businessHours.changeSpecificMode(mode)
        }
      />
    )}
  />
)
