import React from 'react'
import Select from 'react-select'
import { Props } from 'react-select/lib/Select'
import { StylesConfig } from 'react-select/lib/styles'

import { Colors, Spacing } from '../styling'

export interface ISelectControlProps<OptionType> extends Props<OptionType> {
  width?: number
  minWidth?: number
}

export const composeSelectControlStyles = (outerStyles: StylesConfig = {}) => ({
  ...outerStyles,
  control: (base, state) => {
    const {
      selectProps: { width, minWidth },
    } = state
    const controlStyles = {
      ...base,
      border: `1px solid ${Colors.greyMiddle}`,
      margin: Spacing.MarginAroundFormComponents,
      width: width !== undefined ? `${width}rem` : '100%',
      minWidth: minWidth !== undefined ? `${minWidth}rem` : '8rem',
    }
    return outerStyles.control
      ? outerStyles.control(controlStyles, state)
      : controlStyles
  },
  menu: (base, state) => {
    const {
      selectProps: { width },
    } = state
    const menuStyles = {
      ...base,
      zIndex: 20,
      color: Colors.black,
      width: width !== undefined ? `${width}rem` : '100%',
    }
    return outerStyles.menu ? outerStyles.menu(menuStyles, state) : menuStyles
  },
})

export function SelectControl<OptionType>(
  props: ISelectControlProps<OptionType>,
) {
  return (
    <Select<OptionType>
      {...props}
      styles={composeSelectControlStyles(props.styles)}
    />
  )
}
