import React from 'react'

import { NoAccess } from './NoAccess'
import { useRoot } from './RootProvider'

interface IProps {
  mutationName: string
}

export const CanMutate: React.FC<IProps> = ({ mutationName, children }) => {
  const { user } = useRoot()
  return user.canMutate(mutationName) ? (
    <>{children}</>
  ) : (
    <NoAccess>{`No access to mutation ${mutationName}`}</NoAccess>
  )
}
