import React from 'react'

import { NoAccess } from './NoAccess'
import { useRoot } from './RootProvider'

interface IProps {
  queryName: string
}

export const CanQuery: React.FC<IProps> = ({ queryName, children }) => {
  const { user } = useRoot()
  return user.canQuery(queryName) ? (
    <>{children}</>
  ) : (
    <NoAccess>{`No access to query ${queryName}`}</NoAccess>
  )
}
