import React, { createElement, HTMLAttributes } from 'react'
import { Link } from 'react-router-dom'

import { CanMutate } from '../atoms/CanMutate'
import { CanQuery } from '../atoms/CanQuery'

interface IPageLayoutItemProps extends HTMLAttributes<HTMLElement> {
  route?: string
  component: React.ComponentType
  canQuery?: string
  canMutate?: string
}

export const PageLayoutItem: React.FC<IPageLayoutItemProps> = ({
  route,
  component,
  canQuery,
  canMutate,
  ...props
}) => {
  let layoutItem = route ? (
    <Link to={route}>{createElement(component)}</Link>
  ) : (
    createElement(component)
  )

  if (canQuery) {
    layoutItem = <CanQuery queryName={canQuery}>{layoutItem}</CanQuery>
  }
  if (canMutate) {
    layoutItem = <CanMutate mutationName={canMutate}>{layoutItem}</CanMutate>
  }
  return <div {...props}>{layoutItem}</div>
}
