import React from 'react'
import styled from 'styled-components'

import { Spacing } from '../styling'
import { MarginBox } from './MarginBox'

interface IProps {
  width?: number
  widthUnit?: string
  height?: number
  heightUnit?: string
  margin?: string
  className?: string
}

const widthProperty = ({
  width: width,
  widthUnit: widthUnit = Spacing.DefaultSizeUnit,
}: IProps) => {
  if (width === undefined) {
    return ''
  } else {
    return `width: ${width}${widthUnit}`
  }
}

const heightProperty = ({
  height: height,
  heightUnit: heightUnit = Spacing.DefaultSizeUnit,
}: IProps) => {
  if (height === undefined) {
    return ''
  } else {
    return `height: ${height}${heightUnit}`
  }
}

const SizerContainer = styled('div')`
  ${widthProperty};
  ${heightProperty};
`

export const Sizer: React.FC<IProps> = ({
  width,
  widthUnit = Spacing.DefaultSizeUnit,
  height,
  heightUnit = Spacing.DefaultSizeUnit,
  margin,
  children,
  className,
  ...props
}) => {
  const wrap = (
    <SizerContainer
      width={width}
      widthUnit={widthUnit}
      height={height}
      heightUnit={heightUnit}
      className={className}
      {...props}
    >
      {children}
    </SizerContainer>
  )

  return margin === undefined ? (
    wrap
  ) : (
    <MarginBox margin={margin}>{wrap}</MarginBox>
  )
}
