import { Trans } from '@lingui/macro'
import React from 'react'
import { Props } from 'react-select/lib/Select'

import { CompanyBranch } from '../atoms/CompanyBranch'
import { ISelectControlProps, SelectControl } from '../atoms/SelectControl'
import { FCompanyBranchFragment } from '../graph/generated'
import { RM } from '../helpers/ramda'
import { i18n } from '../i18n'
import { ICompanyBranchPickerImpl } from '../organisms/CompanyBranchPicker'

interface IProps
  extends ICompanyBranchPickerImpl,
    Props<FCompanyBranchFragment> {}

export interface ICompanyBranchDropdownProps
  extends Pick<
      ISelectControlProps<FCompanyBranchFragment>,
      'placeholder' | 'width'
    > {}

const ClearIndicator = props => {
  const {
    getStyles,
    innerProps: { ref, ...restInnerProps },
  } = props
  return (
    <div
      {...restInnerProps}
      ref={ref}
      style={getStyles('clearIndicator', props)}
    >
      <div style={{ padding: '0px 5px' }}>
        <Trans>All</Trans>
      </div>
    </div>
  )
}

export const CompanyBranchDropdown: React.FC<IProps> = ({
  branches,
  selectedBranch,
  onSingleSelection,
  selectedBranches,
  onMultiSelection,
  ...props
}) => (
  <SelectControl<FCompanyBranchFragment>
    value={props.isMulti ? selectedBranches : selectedBranch}
    placeholder={
      props.isMulti ? i18n.t`Filter branches...` : i18n.t`Select a branch`
    }
    options={branches as FCompanyBranchFragment[]}
    getOptionValue={RM.prop('id')}
    formatOptionLabel={branch => <CompanyBranch branch={branch} />}
    filterOption={(branch, rawInput) =>
      branch.data.name
        .toLocaleLowerCase()
        .includes(rawInput.toLocaleLowerCase())
    }
    closeMenuOnSelect={!props.isMulti}
    components={{
      ClearIndicator,
    }}
    styles={{
      clearIndicator: base => ({
        ...base,
        cursor: 'pointer',
      }),
    }}
    onChange={selection => {
      if (props.isMulti && onMultiSelection && Array.isArray(selection)) {
        onMultiSelection(selection)
      }
      if (!props.isMulti && onSingleSelection) {
        if (Array.isArray(selection)) {
          onSingleSelection(RM.head(selection) || null)
        } else {
          onSingleSelection(selection || null)
        }
      }
    }}
    {...props}
  />
)
