import { InlineTimePicker, TimePickerProps } from 'material-ui-pickers'
import React from 'react'
import { Field, FieldType } from 'react-form'

import { i18n } from '../i18n'
import { DateTimeFormatProvider } from './DateTimeFormatProvider'

interface IProps extends Omit<TimePickerProps, 'value' | 'onChange'> {
  field: FieldType
}

export const FormInputTime: React.FC<IProps> = ({ field, ...pickerProps }) => {
  return (
    <DateTimeFormatProvider>
      <Field
        field={field}
        render={fieldApi => (
          <InlineTimePicker
            value={fieldApi.value}
            onChange={value => fieldApi.setValue(value)}
            onOpen={() => fieldApi.setTouched(true)}
            format="LT"
            ampm={false}
            okLabel={i18n.t`Ok`}
            cancelLabel={i18n.t`Cancel`}
            clearLabel={i18n.t`Clear`}
            {...pickerProps}
          />
        )}
      />
    </DateTimeFormatProvider>
  )
}
