import React from 'react'

import { CanMutate } from '../../../atoms/CanMutate'
import { FlexColumn } from '../../../atoms/FlexColumn'
import { FlexRow } from '../../../atoms/FlexRow'
import { GSpecificBusinessHour } from '../../../graph/types'
import { i18n } from '../../../i18n'
import { TouchButtonDelete } from '../../../molecules/TouchButtonDelete'

interface ISpecificBusinessHourLineProps {
  bsHour: GSpecificBusinessHour.Fragment
  onDelete(): void
}

export const SpecificBusinessHourLine: React.FC<
  ISpecificBusinessHourLineProps
> = ({ bsHour, onDelete }) => (
  <FlexRow>
    <FlexColumn fixedWidth={true}>
      <CanMutate mutationName="destroyBusinessHour">
        <TouchButtonDelete
          onDelete={onDelete}
          confirmText={`${i18n.t`Really remove specific business hour ${
            bsHour.specificDateLocalized
          }?`}`}
        />
      </CanMutate>
    </FlexColumn>
    <FlexColumn fixedWidth={true} width="14rem">
      {bsHour.specificDateLocalized}
    </FlexColumn>
    <FlexColumn widthGrowIndex={1}>{formatLineTime(bsHour)}</FlexColumn>
  </FlexRow>
)

function formatLineTime(bsHour: GSpecificBusinessHour.Fragment) {
  if (bsHour.closed) {
    return bsHour.closedNote
  }
  return `${bsHour.openAt} - ${bsHour.closeAt}`
}
