import React from 'react'

import { WithRoot } from '../../../atoms/WithRoot'
import { TSettingsModel } from '../models/SettingsModel'

interface IProps {
  render(settings: TSettingsModel): ReactNode
  observe?: boolean
}

export const WithSettings: React.FC<IProps> = ({ observe, render }) => (
  <WithRoot observe={observe} render={({ settings }) => render(settings)} />
)
