import { Trans } from '@lingui/macro'
import React from 'react'
import styled, { css } from 'styled-components'

import { RM } from '../helpers/ramda'

export interface ISelectControlFlatProps<ItemType = unknown> {
  items: ReadonlyArray<ItemType>
  selectedItem?: Maybe<ItemType>
  onSingleSelection?(selected: Maybe<ItemType>): void
  selectedItems?: ReadonlyArray<ItemType>
  onMultiSelection?(selected: ReadonlyArray<ItemType>): void
  formatItem?: (item: ItemType) => string | ReactNode
  isMulti?: boolean
  isClearable?: boolean
  isInvalid?: boolean
  textColor?: string
}

interface IItemProps {
  isSelected: boolean
  textColor: string
  onClick(): void
}

const ItemStyled = styled.div`
  margin: 0.5rem 0.6rem;
  display: inline-block;
  cursor: pointer;
  color: ${({ textColor }: IItemProps) => textColor};
  ${applySelectionStyle};
`

const SelectorStyled = styled.div`
  border: ${({ isInvalid }: { isInvalid: boolean }) =>
    isInvalid === true ? '1px red solid' : 'auto'};
`

export class SelectControlFlat<
  ItemType = { id: ID; displayName: string }
> extends React.Component<ISelectControlFlatProps<ItemType>> {
  static defaultProps = {
    formatItem: RM.prop('displayName'),
    getItemValue: RM.prop('id'),
  }
  hasSelection() {
    const { isMulti, selectedItem, selectedItems } = this.props
    if (isMulti) {
      return Array.isArray(selectedItems) && selectedItems.length > 0
    }
    return selectedItem !== null && selectedItem !== undefined
  }
  isItemSelected(item: ItemType) {
    const { isMulti, selectedItem, selectedItems } = this.props
    if (isMulti && Array.isArray(selectedItems)) {
      return RM.contains(item, selectedItems)
    }
    return RM.equals(selectedItem, item)
  }
  updateSelection(item: Maybe<ItemType>) {
    const {
      isMulti,
      selectedItems = [],
      onSingleSelection,
      onMultiSelection,
    } = this.props
    if (item === null) {
      if (isMulti && onMultiSelection) {
        onMultiSelection([])
      } else if (onSingleSelection) {
        onSingleSelection(null)
      }
    } else if (isMulti && onMultiSelection) {
      if (this.isItemSelected(item)) {
        onMultiSelection(
          Array.isArray(selectedItems)
            ? RM.without([item], selectedItems)
            : [item],
        )
      } else {
        onMultiSelection(
          Array.isArray(selectedItems) ? RM.concat(selectedItems, [item]) : [],
        )
      }
    } else if (onSingleSelection) {
      onSingleSelection(item)
    }
  }
  render() {
    const {
      items,
      isClearable,
      isInvalid = false,
      textColor = 'inherit',
      formatItem,
    } = this.props
    return (
      <SelectorStyled isInvalid={isInvalid}>
        {isClearable && (
          <ItemStyled
            isSelected={!this.hasSelection()}
            textColor={textColor}
            onClick={() => {
              this.updateSelection(null)
            }}
          >
            <Trans id="optionselector.clear-filter">All</Trans>
          </ItemStyled>
        )}
        {items.map((item, idx) => {
          return (
            <ItemStyled
              key={idx}
              isSelected={this.isItemSelected(item)}
              textColor={textColor}
              onClick={() => {
                this.updateSelection(item)
              }}
            >
              {formatItem!(item)}
            </ItemStyled>
          )
        })}
      </SelectorStyled>
    )
  }
}

function applySelectionStyle({ isSelected }: IItemProps) {
  if (!isSelected) {
    return ''
  }
  return css`
    font-weight: bolder;
    text-decoration: underline;
  `
}
