import React from 'react'

import { Colors, Spacing } from '../styling'
import { ITouchButtonProps, TouchButton } from './TouchButton'

interface ITouchButtonDeleteProps extends Omit<ITouchButtonProps, 'onClick'> {
  confirmText?: string
  onDelete(): void
}

export const TouchButtonDelete: React.FC<ITouchButtonDeleteProps> = ({
  onDelete,
  confirmText,
  ...props
}) => (
  <TouchButton
    iconName="delete"
    color={Colors.greyMiddle}
    width={Spacing.ButtonHeight}
    height={Spacing.ButtonHeight}
    onClick={ev => {
      ev.preventDefault()
      if (!confirmText || window.confirm(confirmText)) {
        onDelete()
      }
    }}
    {...props}
  />
)
