import { DatePickerProps, InlineDatePicker } from 'material-ui-pickers'
import React from 'react'
import { Field, FieldType } from 'react-form'

import { i18n } from '../i18n'
import { DateTimeFormatProvider } from './DateTimeFormatProvider'

interface IProps extends Omit<DatePickerProps, 'value' | 'onChange'> {
  field: FieldType
}

export const FormInputDate: React.FC<IProps> = ({ field, ...pickerProps }) => {
  return (
    <DateTimeFormatProvider>
      <Field
        field={field}
        render={fieldApi => (
          <InlineDatePicker
            value={fieldApi.value}
            onChange={value => fieldApi.setValue(value)}
            onOpen={() => fieldApi.setTouched(true)}
            format="ll"
            ampm={false}
            okLabel={i18n.t`Ok`}
            cancelLabel={i18n.t`Cancel`}
            clearLabel={i18n.t`Clear`}
            todayLabel={i18n.t`Today`}
            maxDateMessage={i18n.t`Date is too much in the future`}
            minDateMessage={i18n.t`Date is too much in the past`}
            {...pickerProps}
          />
        )}
      />
    </DateTimeFormatProvider>
  )
}
